.isc {
    display: flex;
    height: 50vh;
    white-space: nowrap;
    overflow-x: auto;
    margin: 0px 300px;
    position: relative;
    overflow-y: hidden
}

.intersectionImageContainer {
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    height: 450px;
}

.intersectionImageList {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 420px;
    box-sizing: border-box;
    white-space: nowrap;
}

.verticalImage {
    transition: all 1s;
    width: 330px;
    height: 330px;
    opacity: 0;
    transform: translateY(20px);
    margin: 33px;
}



.scaleUp {
    scale: 1.1;
}

.verticalImage.show {
    opacity: 1;
    transform: translateY(0);
}

.intersectionImageContainer::-webkit-scrollbar {
    height: 10px;
    background-color: #F90;
}
