.mypage_container {
    display: flex;
    flex-direction: row;
    /* width: calc(100vw - 17px); */
    margin-top: 100px;
    margin-left: 100px;
    height: 55.8vh;

}

.mypage_menu {

    width: 300px;
    text-align: left;
    font-family: "Noto Sans KR", sans-serif;
    margin-top: -50px;
    font-family: 'GmarketSansMedium';

}

.mypage_menu .MyPage_h2 {

    font-size: 1.5em;
    font-style: normal;
    margin-bottom: 15px;

    /* 폰트 설정 */
}



.mypage_menu ul li {
    margin-bottom: 13px;
    text-align: left;
    font-weight: bold;
    font-family: 'LINESeedKR-Bd';


}


.mypage_menu ul li a {
    display: flex;
    color: black;


}

.MyPage_h3 {
    border-bottom: 4px solid black;
    /* 검은색 굵은 선 추가 */
    padding-bottom: 10px;
    /*선 아래 여백 추가 */

}

.user_info {
    margin-left: 50px;
    font-family: "Noto Sans KR", sans-serif;
    text-align: left;
    width: 50%;
    font-family: 'LINESeedKR-Bd';


}


.user_info .row {
    display: flex;
    margin-bottom: 10px;
    /* 각 행 사이의 간격 */
    margin-bottom: 50px;

}

.name,
.Value {
    font-size: 1.5em;
    margin-right: 10px;
    /* 이름과 값 사이의 간격 */
}

.name {
    font-weight: bold;
    width: 100px;
    /* 이름의 너비 고정 */
    font-size: 20px;
}

.Value {
    flex-grow: 1;
    /* 남은 공간을 Value가 채우도록 함 */
    font-size: 15px;

}

.user_info .MyPage_h3 {
    font-size: 2em;
    margin-bottom: 20px;

}

.user_info p {
    font-size: 1.5em;
    margin-bottom: 60px;
}




