.main_container {
    overflow: hidden;
}

.slide_container {
    position: relative;
    width: calc(100vw - 17px);
    height: 100vh;
}

.slide_list {
    display: flex;
    transition: transform 0.7s ease-in-out;
    width: 100%;
    height: 100%;
}

.slides {
    min-width: 100%;
    height: 100%;
}

.slides img {
    width: calc(100vw - 17px);
    height: 100vh;
}

.nonVisible {
    display: none;
}

.backBtn, .forwardBtn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0.4;
    transition: opacity 0.3s ease-in-out;
    cursor: pointer;
}

.backBtn:hover, .forwardBtn:hover {
    opacity: 1;
}

.backBtn {
    right: 90%;
}

.forwardBtn {
    left: 90%;
}
